import { USER_LOGIN, USER_LOGOUT, WISHLIST_RESET } from "./types";
import axios from "axios";

export const user_auto_login = () => async dispatch => {
  const token = localStorage.getItem("token");

  if (!token) {
    return;
  }
  axios.defaults.headers.common["Authorization"] = `Bearer ${token}`;
  let res = null;
  try {
    res = await axios.get("/api/auth/user-info");
    dispatch({
      type: USER_LOGIN,
      payload: {
        token: token,
        userId: res.data.userId,
        name: res.data.name
      }
    });
  } catch (error) {
    console.log(error.message);
  }
};

export const user_logout = () => dispatch => {
  dispatch({
    type: USER_LOGOUT
  });
  dispatch({
    type: WISHLIST_RESET
  });
  localStorage.removeItem("token");
};
