import React, { useEffect, useState } from "react";
import { Navbar, Container, Button, Nav } from "react-bootstrap";
import { Link } from "react-router-dom";
import FacebookLogin from "react-facebook-login";
import axios from "axios";
import { user_auto_login, user_logout } from "../actions/auth";
import { getAllWishList } from "../actions/wishlist";
import { useDispatch, useSelector } from "react-redux";
import { MdExitToApp, MdShoppingCart } from "react-icons/md";

const Navigation = () => {
  const dispatch = useDispatch();
  const [count, setCount] = useState(0);

  const responseFacebook = response => {
    axios
      .post("/api/auth/facebook-login", {
        accessToken: response.accessToken,
        userID: response.userID
      })
      .then(response => {
        localStorage.setItem("token", response.data.token);
        dispatch(user_auto_login());
        dispatch(getAllWishList());
      });
  };
  const { token, name } = useSelector(state => state.userAuth);
  const { wishlist } = useSelector(state => state.wishList);

  const logoutHandler = () => {
    dispatch(user_logout());
  };

  useEffect(() => {
    setCount(wishlist.length);
  }, [wishlist]);

  return (
    <Navbar bg='dark' variant='dark'>
      <Container>
        <Link to='/'>
          <Navbar.Brand>Books.com</Navbar.Brand>
        </Link>
        {token ? (
          <Nav>
            <Nav.Item className='mr-3'>
              <Nav.Link as={Link} to='/wish-list'>
                <MdShoppingCart /> {count}
              </Nav.Link>
            </Nav.Item>
            <Navbar.Brand>{name}</Navbar.Brand>
            <Button variant='primary' onClick={logoutHandler}>
              <MdExitToApp /> Logout
            </Button>
          </Nav>
        ) : (
          <FacebookLogin
            appId={process.env.REACT_APP_FACEBOOK_APP_ID}
            fields='name,email,picture'
            callback={responseFacebook}
          />
        )}
      </Container>
    </Navbar>
  );
};

export default Navigation;
