import {
  GET_WISHLIST,
  ADD_WISHLIST,
  REMOVE_WISHLIST,
  WISHLIST_LOADING,
  WISHLIST_ERROR,
  WISHLIST_RESET
} from "../actions/types";

const initialState = {
  wishlist: [],
  wishListLoading: false,
  wishListError: false
};

export const wishListReducer = (state = initialState, action) => {
  switch (action.type) {
    case GET_WISHLIST:
      return {
        wishlist: action.payload,
        wishListLoading: false,
        wishListError: false
      };
    case ADD_WISHLIST:
      return {
        ...state,
        wishListLoading: false,
        wishListError: false
      };
    case REMOVE_WISHLIST:
      return {
        wishlist: state.wishlist.filter(wish => wish._id !== action.payload),
        wishListLoading: false,
        wishListError: false
      };
    case WISHLIST_LOADING:
      return {
        ...state,
        wishListLoading: true,
        wishListError: false
      };
    case WISHLIST_ERROR:
      return {
        ...state,
        wishListLoading: false,
        wishListError: true
      };
    case WISHLIST_RESET:
      return {
        ...state,
        wishlist: [],
        wishListLoading: false,
        wishListError: false
      };
    default:
      return {
        ...state
      };
  }
};
