import React, { useState, useEffect } from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import { Table, Form, Card, Alert, Button } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import Spinner from "../components/Spinner";
import { Link } from "react-router-dom";
import { getAllBooks, getBookById } from "../actions/book";
import { addWishList } from "../actions/wishlist";
import { WISHLIST_ERROR } from "../actions/types";

function Home() {
  const { books, bookLoading } = useSelector(state => state.booksList);
  const { wishListLoading, wishListError } = useSelector(
    state => state.wishList
  );
  const [bookId, setBookId] = useState("");

  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(getAllBooks());
  }, []);

  const submitHandler = async e => {
    e.preventDefault();
    try {
      if (bookId) {
        dispatch(getBookById(bookId));
      } else {
        dispatch(getAllBooks());
      }
    } catch (error) {}
  };

  const wishListHandler = async id => {
    try {
      dispatch(addWishList(id));
    } catch (err) {
      dispatch({ type: WISHLIST_ERROR });
    }
  };

  return (
    <div className='home'>
      <Card className='px-3 py-2 my-4'>
        <Card.Body>
          <Form onSubmit={submitHandler}>
            <Form.Group controlId='exampleForm.ControlInput1'>
              <Form.Label>Search by ID</Form.Label>
              <Form.Control
                type='text'
                placeholder='Please Enter the ID'
                onChange={e => setBookId(e.target.value)}
              />
              <button
                type='submit'
                onClick={submitHandler}
                className='btn btn-primary my-2'
              >
                Submit
              </button>

              <Link to='/form'>
                <Button variant='success' className='mx-4'>
                  Add Book{" "}
                </Button>
              </Link>
            </Form.Group>
          </Form>
        </Card.Body>
      </Card>
      {wishListError && (
        <Alert variant='danger'>Error adding to wishlist</Alert>
      )}
      {books.length === 0 ? (
        <Alert className='my-3'>
          <h1 className='text-center'>No Books Found</h1>
        </Alert>
      ) : (
        <div className='my-3'></div>
      )}
      {bookLoading || wishListLoading ? (
        <Spinner></Spinner>
      ) : (
        <Table striped bordered hover variant='dark'>
          <thead>
            <tr>
              <th>ID</th>
              <th>Title</th>
              <th>Author</th>
              <th>ISBN</th>
              <th>Publisher</th>
              <th>Publication Year</th>
              <th>last modified date</th>
              <th>created date</th>
              <th>Add to Wishlist</th>
              <th>Edit</th>
            </tr>
          </thead>

          {books.length === 0 ? (
            <></>
          ) : (
            <tbody>
              {books.map(book => {
                return (
                  <tr key={book._id}>
                    <td>{book._id}</td>
                    <td>{book.title}</td>
                    <td>{book.author}</td>
                    <td>{book.isbn}</td>
                    <td>{book.publisher}</td>
                    <td>{book.publication_year}</td>
                    <td>{book.last_modified_date}</td>
                    <td>{book.created_date}</td>
                    <td>
                      <Button
                        variant='danger'
                        onClick={() => wishListHandler(book._id)}
                      >
                        Add
                      </Button>
                    </td>
                    <td>
                      <Link to={`/form/${book._id}`}>
                        <Button variant='primary'>Edit</Button>
                      </Link>
                    </td>
                  </tr>
                );
              })}
            </tbody>
          )}
        </Table>
      )}
    </div>
  );
}

export default Home;
