import {
  GET_ALL_BOOK,
  GET_BOOK_BY_ID,
  ADD_BOOK,
  BOOK_RESET,
  BOOK_REQUEST,
  GET_BOOK_FORM,
  UPDATE_BOOK
} from "../actions/types";

const initialState = {
  books: [],
  bookLoading: true,
  book: {}
};

export const bookReducer = (state = initialState, action) => {
  switch (action.type) {
    case GET_ALL_BOOK:
      return {
        ...state,
        books: action.payload,
        bookLoading: false
      };
    case GET_BOOK_BY_ID:
      return {
        ...state,
        books: action.payload,
        bookLoading: false
      };
    case GET_BOOK_FORM:
      return {
        ...state,
        book: action.payload,
        bookLoading: false
      };

    case ADD_BOOK:
      return {
        ...state,
        bookLoading: false
      };
    case UPDATE_BOOK:
      return {
        ...state,
        books: [
          ...state.books.filter(
            b => b._id !== action.payload._id,
            action.payload
          )
        ],
        bookLoading: false
      };
    case BOOK_RESET:
      return {
        ...state,
        book: {}
      };

    case BOOK_REQUEST:
      return {
        ...state,
        bookLoading: true
      };
    default:
      return {
        ...state
      };
  }
};
