import React, { useState, useEffect } from "react";
import { Container, Form, Card, Button } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import Spinner from "../components/Spinner";
import { getBookForm, addBook, updateBook } from "../actions/book";

const AddForm = props => {
  const [form, setForm] = useState({
    title: "",
    publisher: "",
    isbn: "",
    author: "",
    publication_year: ""
  });

  const { book, bookLoading } = useSelector(state => state.booksList);
  const dispatch = useDispatch();

  useEffect(() => {
    if (props.match.params.bookId) {
      dispatch(getBookForm(props.match.params.bookId));
      setForm({
        title: book.title,
        publisher: book.publisher,
        isbn: book.isbn,
        publication_year: book.publication_year,
        author: book.author
      });
    }
  }, [
    props.match.params.bookId,
    book.title,
    book.author,
    book.isbn,
    book.publication_year,
    book.publisher
  ]);

  const submitHandler = async e => {
    e.preventDefault();
    if (props.match.params.bookId) {
      try {
        dispatch(updateBook(props.match.params.bookId, form));
        props.history.push("/");
      } catch (error) {
        console.log(error.message);
      }
    } else {
      try {
        dispatch(addBook(form));
        props.history.push("/");
      } catch (error) {
        console.log(error.message);
      }
    }
  };

  return (
    <Container>
      {bookLoading ? (
        <Spinner></Spinner>
      ) : (
        <Card className='px-4 py-4'>
          <Form onSubmit={e => submitHandler(e)}>
            <Form.Group>
              <Form.Label>Title</Form.Label>
              <Form.Control
                type='text'
                placeholder='Enter Title'
                value={form.title}
                onChange={e => setForm({ ...form, title: e.target.value })}
              />
            </Form.Group>

            <Form.Group>
              <Form.Label>Author</Form.Label>
              <Form.Control
                type='text'
                placeholder='Enter Author'
                value={form.author}
                onChange={e => setForm({ ...form, author: e.target.value })}
              />
            </Form.Group>
            <Form.Group>
              <Form.Label>Publisher</Form.Label>
              <Form.Control
                type='text'
                placeholder='Enter Publisher'
                value={form.publisher}
                onChange={e => setForm({ ...form, publisher: e.target.value })}
              />
            </Form.Group>
            <Form.Group>
              <Form.Label>ISBN</Form.Label>
              <Form.Control
                type='text'
                placeholder='Enter ISBN'
                value={form.isbn}
                onChange={e => setForm({ ...form, isbn: e.target.value })}
              />
            </Form.Group>
            <Form.Group>
              <Form.Label>Publication Year</Form.Label>
              <Form.Control
                type='text'
                placeholder='Enter Publication Year'
                value={form.publication_year}
                onChange={e =>
                  setForm({ ...form, publication_year: e.target.value })
                }
              />
            </Form.Group>

            <Button variant='primary' type='submit'>
              Submit
            </Button>
          </Form>
        </Card>
      )}
    </Container>
  );
};
export default AddForm;
