import { USER_LOGIN, USER_LOGOUT } from "../actions/types";

const initialState = {
  userId: null,
  name: "",
  token: null
};

export const authReducer = (state = initialState, action) => {
  switch (action.type) {
    case USER_LOGIN:
      return {
        ...state,
        userId: action.payload.userId,
        token: action.payload.token,
        name: action.payload.name
      };
    case USER_LOGOUT:
      return {
        userId: null,
        token: null,
        name: ""
      };
    default:
      return {
        ...state
      };
  }
};
