import axios from "axios";
import {
  GET_WISHLIST,
  WISHLIST_LOADING,
  REMOVE_WISHLIST,
  WISHLIST_ERROR
} from "./types";

export const getAllWishList = () => async dispatch => {
  try {
    dispatch({ type: WISHLIST_LOADING });
    const res = await axios.get("/api/wishlist");
    dispatch({ type: GET_WISHLIST, payload: res.data.book });
  } catch (error) {
    console.log(error.message);
  }
};

export const addWishList = id => async dispatch => {
  try {
    dispatch({ type: WISHLIST_LOADING });
    await axios.post(`/api/wishlist/${id}`);
    const res = await axios.get(`/api/wishlist/`);
    dispatch({ type: GET_WISHLIST, payload: res.data.book });
  } catch (error) {
    dispatch({ type: WISHLIST_ERROR });
  }
};

export const removeWishList = id => async dispatch => {
  try {
    dispatch({ type: WISHLIST_LOADING });
    await axios.delete(`/api/wishlist/${id}`);
    dispatch({ type: REMOVE_WISHLIST, payload: id });
  } catch (error) {
    dispatch({ type: WISHLIST_ERROR });
  }
};
