import {
  GET_ALL_BOOK,
  GET_BOOK_BY_ID,
  ADD_BOOK,
  BOOK_REQUEST,
  GET_BOOK_FORM,
  BOOK_RESET,
  UPDATE_BOOK
} from "./types";
import axios from "axios";

export const getAllBooks = () => async dispatch => {
  try {
    dispatch({ type: BOOK_REQUEST });
    const res = await axios.get(`/api/books/`);
    dispatch({ type: GET_ALL_BOOK, payload: res.data });
  } catch (err) {
    console.log(err.message);
  }
};

export const getBookById = id => async dispatch => {
  try {
    dispatch({ type: BOOK_REQUEST });
    const res = await axios.get(`/api/books/${id}`);
    dispatch({ type: GET_BOOK_BY_ID, payload: res.data.book });
  } catch (err) {
    console.log(err.message);
  }
};

export const getBookForm = id => async dispatch => {
  try {
    dispatch({ type: BOOK_REQUEST });
    const res = await axios.get(`/api/books/${id}`);
    dispatch({ type: GET_BOOK_FORM, payload: res.data.book[0] });
  } catch (err) {
    console.log(err.message);
  }
};

export const addBook = formData => async dispatch => {
  try {
    dispatch({ type: BOOK_REQUEST });
    const res = await axios.post(`/api/books`, formData, {
      "Content-Type": "application/json"
    });
    dispatch({ type: ADD_BOOK, payload: res.data.result });
  } catch (err) {
    console.log(err.message);
  }
};

export const updateBook = (id, formData) => async dispatch => {
  try {
    dispatch({ type: BOOK_REQUEST });
    const res = await axios.put(`/api/books/${id}`, formData, {
      "Content-Type": "application/json"
    });
    dispatch({ type: UPDATE_BOOK, payload: res.data.result });
    dispatch({ type: BOOK_RESET });
  } catch (err) {
    console.log(err.message);
  }
};
